
.mine{
  height: 100vh;
  background: #fff;

  .head{
    height: 100px;
    // background-color: rgb(54, 54, 247);

    .touxiang{
      width: 30%;
      height: 100px;
      // background-color: rgb(133, 231, 147);
      float: left;
      position: relative;
    }

    .name{
      width: 70%;
      height: 100px;
      // background-color: rgb(203, 209, 82);
      float: left;
      font-size: 20px;
      color:#333;
    }
  }

}

.touxiang img{
  display: block;
  width: 70px;
  height: 70px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border-radius: 50%;
  background: url(../../../assets/default.jpg) no-repeat;
  background-size: 100% 100%;
}

.name p{
  height: 100px;
  line-height: 100px;
}

.exit{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;

  .btn{
    width: 90%;
    padding: 0;
  }
}